* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.login-container {
    width: 100vw;
    height: 100vh;
    background-image: url(./img/supermarket.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-form {
    width: 900px;
    height: 1010px;
    padding: 40px;
    border: 2px solid black;
    border-radius: 10px;
    background-color: rgba(173, 255, 47, 0.9);
    /* greenyellow con transparencia */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
}

.login-header {
    color: black;
    text-align: center;
    font-size: 50px;
    margin-bottom: 20px;
}

.divider {
    width: 80%;
    border: 2px solid black;
    margin: 0 auto 20px;
}

.form-group {
    color: black;
    font-size: 50px;
    margin-bottom: 20px;
}

.dropdown-container {
    margin-bottom: 20px;
}

.dropdown {
    width: 100%;
    height: 70px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    border: none;
    padding-left: 10px;
    font-size: 56px;
    appearance: none;
    background-color: white;
    background-image: url("data:image/svg+xml;utf8,<svg fill='black' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/></svg>");
    background-repeat: no-repeat;
    background-position-x: 95%;
    background-position-y: 50%;
}

input {
    width: 100%;
    height: 80px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    font-size: 20px;
    padding: 0 10px;
    border: 1px solid #ccc;
}

.button-container {
    text-align: center;
  font-size: 60px;
align-items: center;
margin-top: -700px;

}

button {
    height: 105px;
    width: 500px;
    background-color: rgb(57, 170, 26);
    border: none;
    border-radius: 10px;
    font-size: 90px;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s;  
  
    
}

button:hover {
    background-color: rgb(45, 140, 20);
}

button a {
    color: white;
    text-decoration: none;
    display: block;
    width: 100%;
    height: 100%;
    line-height: 50px;  margin-top: 35px;
}

.link {
    text-align: center;
    color: black;
    font-size: 56px;
    margin-bottom: 10px;
    cursor: pointer;
    text-decoration: underline;
}

.link.create-profile {
    font-size: 78px;
    font-weight: bold;
    margin-top: 20px; }