body{
    box-sizing:border-box ;
margin: 0;
padding: 0;
font-family: Arial, Helvetica, sans-serif;
color: white;

}
header{
    padding: 30px;
    background: rgb(47, 255, 85);
    height: 100px;
    font-size: 19px;
    display: flex;

}
.sidenav{
   width: 240px;
   background: rgb(47, 255, 85); 
   position: absolute;
   height: 93vh;
   padding: 0 30 px;
   left: -100%;
   transition: all 0.5s;
}
.sidenav.active{
    left: 0;
}
ul{
    padding: 0;
}
ul li{
    list-style-type: none ;
    margin: 10px 0;
    margin-top: 40px;
}
ul li a{
color: aliceblue;
text-decoration: none;
font-size: 34px;
border-radius: 6px;
}

ul li a :hover,
ul li a.active{
    background-color:rgb(47, 255, 85) ;
}
header svg{
    font-size: 45px;
}
.he{
    font-size: 50px;
    color: whitesmoke;
    margin-left: 80%;
    width: 400px;
}
.tabla-container {
    font-family: Arial, sans-serif;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;

}

.titulo-tabla {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
}

.tabla-productos {
    width: 100%;
    border-collapse: collapse;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.encabezado {
    background-color: white;
    padding: 12px 15px;
    text-align: left;
    font-weight: bold;
    border-bottom: 2px solid #ddd;
    color: black;
}

.fila-producto {
    border-bottom: 1px solid #eee;
}

.fila-producto:hover {
    background-color: #f9f9f9;
}

.celda {
    padding: 12px 15px;
    text-align: left;
    color: black;
}

/* Estilo para las filas pares */
.fila-producto:nth-child(even) {
    background-color: #fafafa; }
    .button-panel {
        display: flex;
        flex-direction: column;
        gap: 15px;
        padding: 20px;
        font-family: Arial, sans-serif;
      }
      
      .button-row {
        display: flex;
        gap: 10px;
      }
      
      .panel-button {
        padding: 10px 20px;
        background-color: #f0f0f0;
        border: 1px solid #ccc;
        border-radius: 4px;
        cursor: pointer;
        font-size: 16px;
        transition: background-color 0.3s;
      }
      
      .panel-button:hover {
        background-color: #e0e0e0;
      }
      
      .total-section {
        margin-top: 20px;
        font-weight: bold;
        font-size: 18px;
      }
      .mi{
        color: black;
        width: 2245px;
        height: 55px;
        border: 7px solid black;
        margin-right: 10px;
        margin-top: 27%;
      }
      .ni{
        display: flex;
        height: 200px;
    width: 1144px;  

   
    }
